import React, { useState } from 'react'

interface NavBarProps {

}

const NavBar: React.FC<NavBarProps> = (props) => {
  const [dropdown, setDropdown] = useState(false)
  const [navBar, setNavBar] = useState(false)
  return (
    <nav className="navbar navbar-expand-lg navbar-dark bg-dark">
      <a className="navbar-brand" href="/">Equipo Rojo</a>
      <button className="navbar-toggler" data-toggle="collapse" data-target="/navbarColor02"
        onClick={() => setNavBar(!navBar)}>
        <span className="navbar-toggler-icon"></span>
      </button>

      <div className={`collapse navbar-collapse ${navBar ? 'show' : ''}`} id="navbarColor02">
        <ul className="navbar-nav mr-auto">
          <li className="nav-item active">
            <a className="nav-link" href="/">Home</a>
          </li>
          <li className="nav-item">
            <a className="nav-link" href="/">Features</a>
          </li>
          <li className="nav-item">
            <a className="nav-link" href="/">Diseño de la base de datos</a>
          </li>
          <li className="nav-item dropdown">
            <span className="nav-link dropdown-toggle" role="button"
              aria-haspopup="true" onClick={() => setDropdown(!dropdown)}>Tablas</span>
            <div className={`dropdown-menu ${dropdown ? 'show' : ''}`}>
              <a className="dropdown-item" href="/">Action</a>
              <a className="dropdown-item" href="/">Another action</a>
              <a className="dropdown-item" href="/">Something else here</a>
              <div className="dropdown-divider"></div>
              <a className="dropdown-item" href="/">Separated link</a>
            </div>
          </li>
        </ul>
        <button className="btn btn-outline-primary" type="submit">Log In</button>
      </div>
    </nav>
  )
}

export default NavBar